import { Injectable } from '@angular/core';
import { HttpClient, HttpParams } from '@angular/common/http';
import { map, first } from 'rxjs/operators';

const API_URL = 'api/tacacs/reports/top/access/';

export interface WidgetRequest {
  users: number
  devices: number
  usersReload: number
  devicesReload: number
}

@Injectable({
  providedIn: 'root'
})

export class PieWidget {

  constructor(private http: HttpClient) {}

  get(data:WidgetRequest) {
    const params = new HttpParams()
    .set('users', data.users.toString())
    .set('devices', data.devices.toString())
    .set('usersReload', data.usersReload.toString())
    .set('devicesReload', data.devicesReload.toString())
    return this.http.get<any>(API_URL, { params })
      .pipe(
        first(),
        map( (data) => {
          return data
        } )
      )
  }
}
