import { Component, OnInit } from '@angular/core';
import { BehaviorSubject } from 'rxjs'

//Service
import { LineChartWidget } from './line-chart-widget.service'

@Component({
  selector: 'kt-w-auth-linechart',
  templateUrl: './w-auth-linechart.component.html',
  styleUrls: ['./w-auth-linechart.component.scss']
})
export class WAuthLinechartComponent implements OnInit {

  public barChartOptions_authe = {
    scaleShowVerticalLines: false,
    responsive: true,
    scales: {
      yAxes: [{
        display: true,
        scaleLabel: {
          display: true,
          labelString: 'Authentication'
        },
        ticks: {
          beginAtZero: true,
          userCallback: function(label, index, labels) {
            if (Math.floor(label) === label) {
              return label;
            }
          },
        }
      }]
    } //scales
  };
  public barChartOptions_autho = {
    scaleShowVerticalLines: false,
    responsive: true,
    scales: {
      yAxes: [{
        display: true,
        scaleLabel: {
          display: true,
          labelString: 'Authorization'
        },
        ticks: {
          beginAtZero: true,
          userCallback: function(label, index, labels) {
            if (Math.floor(label) === label) {
              return label;
            }
          },
        }
      }]
    } //scales
  };
  public barChartLabels = [];
  labels = new BehaviorSubject([])
  public barChartType = 'line';
  public barChartLegend = true;
  public barChartData_authe = [
    {data: [], label: 'Fail', fill: false},
    {data: [], label: 'Success', fill: false}
  ];
  public barChartData_autho = [
    {data: [], label: 'Fail', fill: false},
    {data: [], label: 'Success', fill: false}
  ];

  constructor(
    private service: LineChartWidget
  ) { }

  ngOnInit() {
    this.service.get().subscribe( data => {
      // console.log(data)
      this.barChartData_authe[0].data = data.authe.chart.f
      this.barChartData_authe[1].data = data.authe.chart.s
      this.barChartData_autho[0].data = data.autho.chart.f
      this.barChartData_autho[1].data = data.autho.chart.s
      // console.log(data.authe.chart.f)
      this.labels.next(data.time_range)
    })
  }

}
