import { Component, OnInit } from '@angular/core';
import { BehaviorSubject } from 'rxjs'

import { SystemInfoService } from './system-info.service'

@Component({
  selector: 'kt-w-system-info',
  templateUrl: './w-system-info.component.html',
  styleUrls: ['./w-system-info.component.scss']
})
export class WSystemInfoComponent implements OnInit {

  widgets = {
    api: {
      text: new BehaviorSubject('...')
    },
    tac_plus: {
      text: new BehaviorSubject('...')
    },
    users: {
      text: new BehaviorSubject('...'),
      url: '/tacacs/users'
    },
    devices: {
      text: new BehaviorSubject('...'),
      url: '/tacacs/devices'
    },
    bad_authe: {
      text: new BehaviorSubject('...'),
    },
    authe: {
      text: new BehaviorSubject('...'),
      url: '/'
    },
    autho: {
      text: new BehaviorSubject('...'),
      url: '/'
    },
    acc: {
      text: new BehaviorSubject('...'),
      url: '/'
    },
    ha: {
      text: new BehaviorSubject('...'),
      url: '/'
    },
    tac_status: {
      text: new BehaviorSubject('...'),
      url: '/tacacs/config/global'
    },
  }

  constructor(
    private service: SystemInfoService
  ) { }

  ngOnInit() {
    this.service.get().subscribe( data => {
      console.log(data.widgets[0])
      this.widgets.users.text.next(data.widgets[0].users)
      this.widgets.devices.text.next(data.widgets[0].devices)
      this.widgets.api.text.next(data.widgets[0].APIVER)
      this.widgets.tac_plus.text.next(data.widgets[0].TACVER)
      this.widgets.bad_authe.text.next(data.widgets[0].authe_err)
      this.widgets.authe.text.next(data.widgets[0].authe)
      this.widgets.autho.text.next(data.widgets[0].autho)
      this.widgets.acc.text.next(data.widgets[0].acc)
      this.widgets.ha.text.next(data.widgets[0].ha)
      this.widgets.tac_status.text.next(data.widgets[0].tac_status)
    })
  }

}
