import { Component, OnInit } from '@angular/core';

import { BehaviorSubject } from 'rxjs'

import { ChartDataSets, ChartOptions } from 'chart.js';
import { Color, BaseChartDirective, Label } from 'ng2-charts';
//Service
import { WidgetRequest, PieWidget } from './pie-widget.service'

@Component({
  selector: 'kt-w-auth-piechart',
  templateUrl: './w-auth-piechart.component.html',
  styleUrls: ['./w-auth-piechart.component.scss']
})
export class WAuthPiechartComponent implements OnInit {

  chartColors = [
    'rgb(255, 99, 132)',
  	'rgb(255, 205, 86)',
  	'rgb(75, 192, 192)',
    'rgb(201, 203, 207)',
  	'rgb(54, 162, 235)',
  	'rgb(153, 102, 255)',
  	'rgb(255, 159, 64)',
  ];

  public barChartLabels_devices = [];
  public barChartLabels = [];
  device_labels = new BehaviorSubject(this.barChartLabels_devices)
  user_labels = new BehaviorSubject(this.barChartLabels)

  public barChartOptions = {
    scaleShowVerticalLines: false,
    responsive: true
  };


  public barChartType = 'pie';
  public barChartLegend = true;
  public barChartData = [
    {data: [], label: 'Users', backgroundColor: this.chartColors}
  ];
  public barChartData_devices = [
    {data: [], label: 'Devices', backgroundColor: this.chartColors}
  ];

  private set: WidgetRequest = {
    users: 5,
    devices: 5,
    usersReload: 1,
    devicesReload: 1
  }

  constructor(
    private service: PieWidget
  ) { }

  ngOnInit() {
    this.service.get(this.set).subscribe( data => {
      this.barChartLabels = []
      this.barChartData[0].data = []
      this.barChartLabels_devices = []
      this.barChartData_devices[0].data = []
      for (let i = 0; i < data.topUsers.length; i++) {
        this.barChartLabels.push(data.topUsers[i].label)
        this.barChartData[0].data.push(data.topUsers[i].count)
      }
      for (let i = 0; i < data.topDevices.length; i++) {
        this.barChartLabels_devices.push(data.topDevices[i].label)
        this.barChartData_devices[0].data.push(data.topDevices[i].count)
      }
      this.device_labels.next(this.barChartLabels_devices)
      this.user_labels.next(this.barChartLabels)
      // console.log(Color)
      // this.chart_devices.update();
      // console.log(this.barChartLabels_devices)
      // console.log(this.barChartData_devices)
    })
  }

}
